import lineRightArrow from '@/static/mbcImg/images/common/line-right-arrow.png';
import xing from '@/static/mbcImg/images/common/xing.png';
import { mapMutations, mapGetters } from 'vuex';
import wInput from '@/components/common/watch-login/watch-input1.vue';
import provinceCity from '@/components/common/provinceCity/provinceCity.vue';
export default {
	name: 'addressBs',
	components: {
		wInput,
		provinceCity
	},
	data() {
		return {
			text: '',
			lineRightArrow: lineRightArrow,
			xing: xing,
			addressDetail: '',
			info: {
				idStringAddress: '', // 用户地址省市区和详细地址拼接
				idString: '', // 地址显示省市区
				addrPcode: '', // 省
				addrCcode: '', // 市
				addrAcode: '', // 区
				idaddress: '' // 详细地址
			},
			province: [], // 全国省
			city: [], // 全国市
			county: [], // 全国县区
			imgData: {},
			arr:[],
			listData: {
				// 省市S
				multiArray: [
				],
				multiIndex: [0, 0],
				areaPorC: {}, // 获取缓存的省市
				province: { // 用户选择的省份
					index: 0, // 记录用户选择省份index
					text: '', // 页面显示字段
					pcode: '' // 传给后台参数
				},
				city: { // 用户选择的城市
					text: '', // 页面显示字段
					ccode: '' // 传给后台参数
				},
				paramsPC: { // 用于接口数据省和市
					ptext: '', // 页面显示字段
					pcode: '' ,// 传给后台参数
					ctext: '', // 页面显示字段
					ccode: '' // 传给后台参数
				},
				// 省市E
			}
		};
	},
	created () {
		
	},
	mounted() {
		
	},
	computed: {
		i18n () {
		  return this.$t('qualificatCert')  
		},
		...mapGetters(['PERSONCENTER', 'IMGDATA'])
	},
	methods: {
		...mapMutations({
			setAddressBsShow: 'setAddressBsShow',
		}),
		clickCity:function(clickCity){
			this.arr=clickCity
			console.log(this.arr)
		},
		clickReset() {
			console.log('取消');
			uni.navigateBack({
				
			})
		},
		clickSaveAddress() {
			console.log('触发保存地址按钮');
			if(this.arr==''){
				uni.showToast({
					title: '请选择地区',
					icon: 'none',
					duration: 1000
				});
				return false;
			}else if(this.info.idaddress==''){
				uni.showToast({
					title: '请输入详细地址',
					icon: 'none',
					duration: 1000
				});
				return false;
			}
			let obj={
				idString: this.arr.cityText, // 地址显示省市区
				addrPcode: this.arr.pcode, // 省
				addrCcode: this.arr.ccode, // 市
				addrTcode: this.arr.tcode, // 区
				idaddress: this.info.idaddress, // 详细地址
			}
			this.$store.commit('setclickCity', obj); // 更新vuex setAddShow
			uni.navigateBack({
				
			})
		}
	}
};
